import React from "react";
import ProfileImg from "./ProfileImg";
import {useSelector} from "react-redux";

const ChatHeader = ({isVisibleMenu, toggleIsVisibleMenu}) => {
    const activeContact = useSelector(state => state.contacts.activeContact);

    return (
        <div className="chat__header">
            <button className="sidebar__button" onClick={() => toggleIsVisibleMenu(!isVisibleMenu)}>
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512">
                    <path
                        d="M492,236H20c-11.046,0-20,8.954-20,20c0,11.046,8.954,20,20,20h472c11.046,0,20-8.954,20-20S503.046,236,492,236z"/>
                    <path
                        d="M492,76H20C8.954,76,0,84.954,0,96s8.954,20,20,20h472c11.046,0,20-8.954,20-20S503.046,76,492,76z"/>
                    <path d="M492,396H20c-11.046,0-20,8.954-20,20c0,11.046,8.954,20,20,20h472c11.046,0,20-8.954,20-20
			C512,404.954,503.046,396,492,396z"/>
                </svg>
            </button>
            <div className="contacts__item">
                <ProfileImg contact={activeContact}/>
                <div>
                    <p>{activeContact.name}</p>
                </div>
            </div>
        </div>
    );
};

export default ChatHeader;
