const initialState = {
    contactsList: [
        {
            id: '1',
            name: "Alice Freeman",
            img: 'data:image/jpeg;base64,',
            isOnline: true,
        },
        {
            id: '2',
            name: "Josefina",
            img: 'data:image/jpeg;base64,',
            isOnline: true,
        },
        {
            id: '3',
            name: "Velazquez",
            img: 'data:image/jpeg;base64,',
            isOnline: true,
        },
        {
            id: '4',
            name: "Barrera",
            img: 'data:image/jpeg;base64,',
            isOnline: true,
        },
    ],
    activeContact: {
        id: '1',
        name: "Alice Freeman",
        img: 'data:image/jpeg;base64,',
        isOnline: true,
    },
};

export default function (state = initialState, action) {
    switch (action.type) {
        case 'SET_ACTIVE':
            return {
                ...state,
                activeContact: action.payload,
            };
        default:
            return state;
    }
}
